'use strict';

import compareme from 'compareme';

const getInvalidOptions = (defaultOptions = {}, options = {}) => {
    return compareme
        .get(defaultOptions)
        .unexpected.elements.strictly.and.deeply.with(options)
        .map((diff) => ({
            key: diff.index,
        }));
};

const getWrongTypeOptions = (defaultOptions = {}, options = {}) => {
    return compareme
        .get(defaultOptions)
        .type.differences.strictly.and.deeply.with(options)
        .map((diff) => ({
            key: diff.index,
            expected: diff.first,
            actual: diff.second,
        }));
};

const validate = (defaultOptions = {}, options = {}) => {
    getWrongTypeOptions(defaultOptions, options).forEach((diff) => {
        console.error(
            `[querystringme] The option "${diff.key}" is expected to be ` +
                `"${diff.expected}" but received as "${diff.actual}".`,
        );
    });

    getInvalidOptions(defaultOptions, options).forEach((diff) => {
        console.error(`[querystringme] The option "${diff.key}" is not valid.`);
    });
};


const merge = (defaultOptions = {}, options = {}) => {
    validate(defaultOptions, options);
    return Object.assign({}, defaultOptions, options);
};

export {
    validate,
    merge,
};
